'use client';

import React from 'react';
import { useRouter } from 'next/navigation';
import { useAppContext } from '../context/AppContext';
import Header from './Header';
import Footer from './Footer';
import { calculateCartTotal, calculateShippingCost, calculateOriginalCartTotal, calculateDiscountSavings } from '../utils/cartUtils';
import {
  CartContainer,
  CartHeaderSection,
  EmptyCartSection,
  EmptyCartContent,
  CartItemsSection,
  CartItemsHeader,
  CartItemsList,
  CartItem,
  ItemProduct,
  ItemImage,
  ItemDetails,
  ItemPrice,
  ItemQuantity,
  ItemTotal,
  ItemActions,
  CartSummarySection,
  CartSummary,
  SummaryDetails,
  SummaryActions
} from '../styles/ElegantCartStyles';

const EnhancedCartPage = () => {
  const router = useRouter();

  const navigate = (path: string) => {
    router.push(path);
  };
  
  const { cartItems, loading, error, updateCartItem, removeFromCart } = useAppContext();

  const updateQuantity = (productId: number, newQuantity: number) => {
    if (newQuantity < 1) {
      // If quantity is less than 1, remove the item
      removeFromCart(productId);
      return;
    }
    updateCartItem(productId, newQuantity);
  };

  const subtotal = calculateCartTotal(cartItems);
  const shipping = calculateShippingCost(subtotal);
  const total = subtotal + shipping;

  const handleCheckout = () => {
    if (cartItems.length > 0) {
      navigate('/checkout');
    }
  };

  const continueShopping = () => {
    navigate('/shop');
  };

  if (cartItems.length === 0) {
    return (
      <CartContainer>
        <Header activePage="cart" />
        <CartHeaderSection>
          <h1>Shopping Cart</h1>
        </CartHeaderSection>
        <EmptyCartSection>
          <EmptyCartContent>
            <i className="fas fa-shopping-cart empty-cart-icon"></i>
            <h2>Your cart is empty</h2>
            <p>Looks like you haven't added any items to your cart yet.</p>
            <button className="btn primary" onClick={continueShopping}>
              Continue Shopping
            </button>
          </EmptyCartContent>
        </EmptyCartSection>
        <Footer />
      </CartContainer>
    );
  }

  return (
    <CartContainer>
      <Header activePage="cart" />
      <CartHeaderSection>
        <h1>Shopping Cart</h1>
      </CartHeaderSection>
      
      {error.cart && <div className="error-message">{error.cart}</div>}
      
      <CartItemsSection>
        <CartItemsHeader>
          <div className="header-item">Product</div>
          <div className="header-price">Price</div>
          <div className="header-quantity">Quantity</div>
          <div className="header-total">Total</div>
          <div className="header-actions"></div>
        </CartItemsHeader>
        
        <CartItemsList>
          {cartItems.map((item, index) => {
            // Create a unique key to avoid duplicate key errors
            // Use product_id first (most reliable), then id, then index as fallback
            const uniqueKey = item.product_id ? `prod-${item.product_id}` : 
                             item.id ? `item-${item.id}-${index}` : 
                             `idx-${index}`;
            
            const productId = item.product_id || item.id;
            
            if (!productId) {
              console.error('Cart item missing product_id and id:', item);
              return null;
            }
            
            return (
              <CartItem key={uniqueKey}>
                <ItemProduct>
                  <ItemImage $imageClass={item.imageClass || 'modern'} />
                  <ItemDetails>
                    <h3>{item.name}</h3>
                    <p>{item.description}</p>
                  </ItemDetails>
                </ItemProduct>
                
                <ItemPrice>
                  â‚¹{typeof item.price === 'number' 
                    ? item.price.toLocaleString() 
                    : parseFloat(item.price || '0').toLocaleString()}
                </ItemPrice>
                
                <ItemQuantity>
                  <button 
                    className="quantity-btn"
                    onClick={() => updateQuantity(productId, item.quantity - 1)}
                    disabled={loading.cart || item.quantity <= 1}
                  >
                    <i className="fas fa-minus"></i>
                  </button>
                  <span className="quantity">{item.quantity}</span>
                  <button 
                    className="quantity-btn"
                    onClick={() => updateQuantity(productId, item.quantity + 1)}
                    disabled={loading.cart}
                  >
                    <i className="fas fa-plus"></i>
                  </button>
                </ItemQuantity>
                
                <ItemTotal>
                  â‚¹{typeof item.price === 'number' 
                    ? (item.price * item.quantity).toLocaleString() 
                    : (parseFloat(item.price || '0') * item.quantity).toLocaleString()}
                </ItemTotal>
                
                <ItemActions>
                  <button 
                    className="remove-btn"
                    onClick={() => removeFromCart(productId)}
                    disabled={loading.cart}
                  >
                    <i className="fas fa-trash-alt"></i>
                  </button>
                </ItemActions>
              </CartItem>
            );
          })}
        </CartItemsList>
      </CartItemsSection>
      
      <CartSummarySection>
        <CartSummary>
          <h2>Order Summary</h2>
          
          <SummaryDetails>
            <div className="summary-row">
              <span>Original Price</span>
              <span>â‚¹{calculateOriginalCartTotal(cartItems).toLocaleString()}</span>
            </div>
            <div className="summary-row" style={{ color: '#28a745', fontWeight: '600' }}>
              <span>You saved</span>
              <span>-â‚¹{calculateDiscountSavings(cartItems).toLocaleString()}</span>
            </div>
            <div className="summary-row">
              <span>Subtotal</span>
              <span>â‚¹{subtotal.toLocaleString()}</span>
            </div>
            
            <div className="summary-row">
              <span>Shipping</span>
              <span>{shipping === 0 ? 'FREE' : `â‚¹${shipping.toLocaleString()}`}</span>
            </div>
            
            <div className="summary-row total">
              <span>Total</span>
              <span>â‚¹{total.toLocaleString()}</span>
            </div>
          </SummaryDetails>
          
          <SummaryActions>
            <button className="btn secondary" onClick={continueShopping}>
              <i className="fas fa-arrow-left"></i> Continue Shopping
            </button>
          </SummaryActions>
        </CartSummary>
        <button 
          className="btn primary" 
          onClick={handleCheckout}
          disabled={loading.cart || cartItems.length === 0}
          style={{ marginTop: '20px', width: '100%', maxWidth: '450px' }}
        >
          Proceed to Checkout
        </button>
      </CartSummarySection>
      
      <Footer />
    </CartContainer>
  );
};

export default EnhancedCartPage;